import datepicker from 'src/Datepicker.vue'
  import select from 'src/Select.vue'
  import option from 'src/Option.vue'
  export default {
    components: {
      datepicker,
      'v-select': select,
      'v-option': option
    },
    data() {
      return {
        disabled: [],
        value: 'Oct/06/2015',
        format: ['MMM/dd/yyyy']
      }
    },
    watch: {
      disabled() {
        this.$refs.dp.getDateRange()
      },
      format(newV) {
        this.value = this.$refs.dp.stringify(new Date(this.value))
      }
    }
  }


